.flex-container{
  display:flex;
  width: 100%;
  height: 100%;
}
.flex-container div{
  width:100%;
  margin-bottom: 10px;
  text-align: center;
}
#costDiv{
  text-align: center;
}

#inputQuantity{
  width: 100%;
}
#inputQuantity input{
  width: 50%;
  height: 100;
  padding: 1px;
  border: 1px solid #eee3e7;
}
#buttonDiv{
  text-align: center;
}
#billDiv{
  width: 100%;
  word-spacing: 1em;
}


@media only screen and (min-width: 600px) {
  .flex-container{
    display:flex;
    width: 100%;
    height: 100%;
  }
  .flex-container div{
    width:100%;
    margin-bottom: 10px;
    text-align: center;
  }
  #buttonDiv{
    padding: 5% 2% 10% 2%;
  }
  #billDiv{
    padding-top: 5%;
  }
}

@media only screen and (min-width: 768px) {
  .flex-container{
    display:flex;
    width: 100%;
    height: 100%;
  }
  .flex-container div{
    width:100%;
    margin-bottom: 10px;
    text-align: center;
  }
  #buttonDiv{
    padding: 5% 2% 10% 2%;
  }
  #billDiv{
    padding-top: 5%;
  }
  #billDiv h4{
    font-size: 150%;
  }
  #buttonDiv button{
    font-size: 150%;
  }
  #buttonDiv h2{
    font-size: 220%;
  }
  body{
    font-size: 125%;
  }
  body h5{
    font-size: 150%;
  }
}
